.container {
    width: 400px;
    /*height: 350px;*/
    border: 1px solid #bfbfbf;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 20px);
    border-radius: 5px 5px;
}

.header {
    height: 50px;
    text-align: center;
    line-height: 50px;
    border-bottom: 1px dotted #bfbfbf;
    color: #4abeec;
    font-weight: 700;
    font-size: 20px;
}

.detail-box {
    height: 70px;
    display: flex;
    border-bottom: 1px dotted #bfbfbf;
    align-items: center;
}

.detail-box .title {
    color: #4abeec;
    font-size: 14px;
}

.basket {
    padding: 20px;
}

.basket .apple-item {
    padding: 10px;
    border: 1px solid #bfbfbf;
    border-radius: 5px 5px;
}


.left, .right {
    flex: 1;
}

.apple-item {
    margin: 10px auto;
    border-radius: 4px;
    border: 1px solid #ddd;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.apple-item .apple {
    flex-grow: 2;
}

.apple-item .info {
    flex-grow: 15;
}
.apple-item .info .name {
    padding: 6px 0;
    font-size: 20px;
    color: #069;
    font-weight: 500;
}
.apple-item .info .weight {
    font-size: 16px;
    font-weight: 200;
}

.apple-item .btn-div {
    flex-grow: 2;
}
.btn-div {
    text-align: center;
}
.btn-div button {
    border: none;
    background-color: #3498db;
    color: #fff;
    font-size: 14px;
    cursor: pointer;

    padding: 10px 24px;
    border-radius: 6px;
    margin: 18px auto;
    outline: none;
}
.btn-div button:hover {
    background-color: #5dade2
}
